import React from "react";
import Href from "../../href";

const Tag = ({ tag, url, newTab }) => (
  <span className="inline-block bg-gray-200 rounded-full px-3 py-1 text-sm font-semibold text-gray-700 mr-2 hover:underline">
    <Href url={url} newTab={newTab}>
      {tag}
    </Href>
  </span>
);

const Card = ({ title, image, url, link, tags, newTab }) => (
  <div className="max-w-sm rounded overflow-hidden shadow-lg">
      {typeof image !== "object" ? <img src={image} alt={title} className="w-full" /> : image}
    <div className="px-6 py-4">
      <div className="font-bold text-xl mb-2 hover:underline">
        <Href url={url} link={link} newTab={newTab}>
          {title}
        </Href>
      </div>
    </div>
    {tags && (
      <div className="px-6 py-4">
        {tags.split(",").map(tag => (
          <Tag
            tag={`#${tag.toLowerCase()}`}
            url={`https://pilab.dev/tag/${tag.toLowerCase().replace(" ", "-")}`}
            newTab={true}
          />
        ))}
      </div>
    )}
  </div>
);

export default Card;
